@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  color: #101010; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1;
  margin-top: -88px;
  overflow: hidden; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 165px;
  margin: 0 auto; }

.header {
  position: relative;
  padding-top: 50px; }
  .header__wrapper {
    display: flex;
    align-items: center; }
  .header__logo {
    position: relative;
    font-weight: bold;
    font-size: 18px;
    line-height: 1.2;
    padding-left: 36px;
    margin-right: 162px;
    text-transform: uppercase;
    color: #ffffff; }
    .header__logo::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      width: 26px;
      height: 19px;
      background-image: url("../img/cloud.svg");
      background-repeat: no-repeat;
      background-size: contain; }
  .header__nav {
    margin-right: auto; }
  .header__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center; }
  .header__item {
    margin-right: 20px; }
    .header__item:last-of-type {
      margin-right: 0; }
  .header__link {
    font-size: 14px;
    line-height: 1.2;
    color: #ffffff;
    text-transform: uppercase; }
    .header__link.active {
      font-weight: bold; }

.btn {
  font-size: 14px;
  line-height: 1.2;
  width: 160px;
  min-height: 50px;
  padding: 10px 28px;
  color: #ffffff;
  border: 1px solid;
  border-color: #ffffff;
  border-radius: 6px;
  text-transform: uppercase; }
  .btn--theme-transparant {
    width: 100px;
    min-height: 40px;
    background-color: transparent; }
  .btn--theme-white {
    background-color: #ffffff;
    color: #ED4C5C; }
  .btn--theme-colored {
    background-color: #ED4C5C;
    border-color: #ED4C5C; }

.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 782px;
  padding: 116px 50px 50px;
  color: #ffffff;
  background: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url("../img/bg@1x.jpg"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .intro {
      background: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url("../img/bg@2x.jpg");
      background-size: cover; } }
  .intro__title {
    font-size: 72px;
    line-height: 86px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin: 0 0 40px; }
  .intro__subtitle {
    font-size: 24px;
    line-height: 1.5;
    max-width: 486px;
    margin: 0 0 54px;
    text-align: center; }
